/**
 * Valida os dados do pedido
 * @param {Object} orderData - Dados do pedido a serem validados
 * @returns {string|null} Mensagem de erro ou null se vlido
 */
export function validateOrder(orderData) {
  // Validao do nmero do pedido
  if (!orderData.orderNumber?.trim()) {
    return 'O nmero do pedido  obrigatrio';
  }

  // Validao da data
  if (!orderData.date) {
    return 'A data  obrigatria';
  }

  // Validao do formato da data (YYYY-MM-DD)
  const dateRegex = /^\d{4}-\d{2}-\d{2}$/;
  if (!dateRegex.test(orderData.date)) {
    return 'Formato de data invlido. Use YYYY-MM-DD';
  }

  // Validao da taxa (se fornecida)
  if (orderData.fee !== undefined && orderData.fee !== null && orderData.fee !== '') {
    const fee = parseFloat(orderData.fee);
    if (isNaN(fee)) {
      return 'Taxa invlida';
    }
    if (fee < 0) {
      return 'A taxa no pode ser negativa';
    }
  }

  // Validao da imagem
  if (!orderData.image) {
    return 'O comprovante  obrigatrio';
  }

  // Se chegou at aqui, no h erros
  return null;
}

/**
 * Valida os dados de gasolina
 * @param {Object} gasData - Dados de gasolina a serem validados
 * @returns {string|null} Mensagem de erro ou null se vlido
 */
export function validateGasEntry(gasData) {
  // Validao da data
  if (!gasData.date) {
    return 'A data  obrigatria';
  }

  // Validao do formato da data (YYYY-MM-DD)
  const dateRegex = /^\d{4}-\d{2}-\d{2}$/;
  if (!dateRegex.test(gasData.date)) {
    return 'Formato de data invlido. Use YYYY-MM-DD';
  }

  // Validao do valor
  if (!gasData.amount) {
    return 'O valor  obrigatrio';
  }

  const amount = parseFloat(gasData.amount);
  if (isNaN(amount)) {
    return 'Valor invlido';
  }

  if (amount <= 0) {
    return 'O valor deve ser maior que zero';
  }

  // Se chegou at aqui, no h erros
  return null;
}

/**
 * Valida o formato de uma data
 * @param {string} date - Data a ser validada
 * @returns {boolean} true se a data  vlida
 */
export function isValidDate(date) {
  // Verifica o formato YYYY-MM-DD
  if (!/^\d{4}-\d{2}-\d{2}$/.test(date)) {
    return false;
  }

  // Verifica se  uma data vlida
  const d = new Date(date);
  if (isNaN(d.getTime())) {
    return false;
  }

  // Verifica se no  uma data futura
  const today = new Date();
  today.setHours(23, 59, 59, 999);
  if (d > today) {
    return false;
  }

  return true;
}

/**
 * Valida um nmero de pedido
 * @param {string} orderNumber - Nmero do pedido a ser validado
 * @returns {boolean} true se o nmero  vlido
 */
export function isValidOrderNumber(orderNumber) {
  // Remove espaos em branco
  const trimmed = orderNumber.trim();
  
  // Verifica se no est vazio
  if (!trimmed) {
    return false;
  }

  // Verifica o comprimento mnimo e mximo
  if (trimmed.length < 1 || trimmed.length > 20) {
    return false;
  }

  // Verifica se contm apenas caracteres vlidos
  // Permite nmeros, letras, traos e underscores
  return /^[a-zA-Z0-9-_]+$/.test(trimmed);
}

/**
 * Valida um valor monetrio
 * @param {string|number} value - Valor a ser validado
 * @returns {boolean} true se o valor  vlido
 */
export function isValidMoneyValue(value) {
  // Se for string, converte para nmero
  const number = typeof value === 'string' ? parseFloat(value) : value;
  
  // Verifica se  um nmero vlido
  if (isNaN(number)) {
    return false;
  }

  // Verifica se  no-negativo
  if (number < 0) {
    return false;
  }

  // Verifica se tem no mximo 2 casas decimais
  const decimalPlaces = (number.toString().split('.')[1] || '').length;
  if (decimalPlaces > 2) {
    return false;
  }

  return true;
} 